* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Poppins", sans-serif;
}

body {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  background: linear-gradient(20deg, blue, #c31432);
}

.login-form {
  width: 420px;
  background: transparent;
  border: 2px solid rgba(255, 255, 255, 0.2);
  color: #fff;
  border-radius: 15px;
  padding: 30px 40px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
}

.login-form h1 {
  font-size: 36px;
  text-align: center;
  margin: 0 0 30px;
}

.login-form input::placeholder {
  color: #fff;
}

.login-form .remember-forgot {
  display: flex;
  justify-content: space-between;
  font-size: 14px;
  margin: 15px 0 15px;
}

.remember-forgot a {
  color: #fff;
  text-decoration: none;
}

.remember-forgot a:hover {
  text-decoration: underline;
}

.login-form .register-link {
  font-size: 14px;
  text-align: center;
  margin: 25px 0 15px;
}

.register-link p a {
  color: #fff;
  text-decoration: none;
  font-weight: 600;
}

.register-link p a:hover {
  text-decoration: underline;
}

.login-form .input-box {
  width: 100%;
  height: 50px;
  margin: 15px auto 35px;
}

.input-box input {
  width: 100%;
  height: 100%;
  background: transparent;
  border: none;
  outline: none;
  border: 2px solid rgba(255, 255, 255, 0.2);
  border-radius: 30px;
  font-size: 16px;
  color: #fff;
  padding: 20px 45px 20px 20px;
}

.login-form .btn {
  width: 100%;
  height: 45px;
  background: #fff;
  border: none;
  outline: none;
  border-radius: 40px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  cursor: pointer;
  font-size: 16px;
  color: #333;
  font-weight: 600;
}

.login-form .btn:disabled {
  background: rgba(255, 255, 255, 0.1);
}

.error {
  margin: 0 0 0 10px;
  color: red;
}
